/* ---------------------------
   Base Reset & Defaults
----------------------------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
  color: #333;
  line-height: 1.6;
  padding-top: 160px; /* Account for fixed header height */
  overflow-x: hidden; /* Prevent horizontal scroll */
}

/* Theme tokens */
:root{
  --vh-accent: #1a73e8;
  --vh-accent-2: #34a853;
  --vh-ink: #202124;
  --vh-muted: #5f6368;
  --vh-surface: #ffffff;
  --vh-surface-2: #f8fbff;
  --vh-ring: 0 0 0 3px rgba(26,115,232,.18);
}

/* Subtle page background and spacing harmony */
body{
  background: radial-gradient(1200px 600px at 10% -10%, #f5f8ff 0%, transparent 40%),
              radial-gradient(1000px 500px at 110% 0%, #f7fff8 0%, transparent 35%),
              #f9f9f9;
  margin: 0;
  box-sizing: border-box;
}

/* Common wrappers */
.results-page,
.google-style-app {
  min-height: calc(100vh - 160px); /* Account for fixed header */
  display: flex;
  flex-direction: column;
}

/* Ensure html and root elements don't conflict with fixed header */
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Fixed header positioning adjustments */
.search-landing {
  min-height: calc(100vh - 160px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  margin-top: -160px; /* Offset the body padding for centering */
  padding-top: 180px; /* Add back the space for header */
}

/* Loading spinner animation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}